.todo-item {
    font-size: 1.25rem;
  }
  
  .todo-item button {
    width: 35px;
    margin: 3px;
  }
  
  .todo-item-name {
    margin-left: 1.25rem;
    line-height: 35px;
    cursor: pointer;
    user-select: none;
  }
  
  .todo-item.done .todo-item-name {
    text-decoration: line-through;
  }
  
  .todo-item.important .todo-item-name {
    font-weight: bold;
    color: steelblue;
  }
  